<template>
  <div id="app">
    <Map v-if="isLoaded" :sensor_1="sensor_1" :sensor_2="sensor_2" :sensor_3="sensor_3" :sensor_4="sensor_4" :sensor_5="sensor_5" :sensor_6="sensor_6" :locations="locations" />
  </div>
</template>

<script>
/* eslint-disable */

import axios from "axios";
import Map from "./components/Map.vue";
import data from "./components/data.json";

export default {
  name: "App",
  components: {
    Map,
  },
  data() {
    return {
      noise: null,
      pollution: null,
      traffic: null,
      body: data,
      locations: null,
      sensor_1: [],
      sensor_2: [],
      sensor_3: [],
      sensor_4: [],
      sensor_5: [],
      sensor_6: [],
      isLoaded: false
    };
  },
  async mounted() {
    const {
      data: { noise },
    } = this.body;
    const {
      data: { pollution },
    } = this.body;
    const {
      data: { traffic },
    } = this.body;
    this.noise = noise;
    this.pollution = pollution;
    this.traffic = traffic;
    let vm = this;

    await axios
      .get("https://drama.smartiscity.gr/api/api.php?func=locations")
      .then((res) => {
       vm.locations = res.data;
      });

    await axios
      .get("https://drama.smartiscity.gr/api/api.php?func=allsensors")
      .then((res) => {
        console.log("all sensors", res.data);
        res.data.forEach((el) => {
          switch (el.sensortype_id) {
            case "1":
              vm.sensor_1.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
            case "2":
              vm.sensor_2.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
            case "3":
              vm.sensor_3.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
            case "4":
              vm.sensor_4.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
            case "5":
              vm.sensor_5.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
            case "6":
              vm.sensor_6.push({...el, coordinates: [parseFloat(el.location_lat), parseFloat(el.location_lng)]});
              break;
          }
        });
      }).catch((err) => {
        vm.isLoaded = false;
      }).finally(() => {
        vm.isLoaded = true;
      });

  },
};
</script>

<style>
#app {
  margin: auto;
  padding: auto;
}
</style>
