<script>
/* eslint-disable */

import {
  LMap,
  LTileLayer,
  LControlZoom,
  LMarker,
  LIcon,
  LPolygon,
} from "vue2-leaflet";
import Vue2LeafletMarkerCluster from "vue2-leaflet-markercluster";
import L from "leaflet";
import _ from "lodash";
import axios from "axios";

export default {
  name: "MyMap",
  components: {
    LMap,
    LTileLayer,
    LControlZoom,
    LMarker,
    LIcon,
    "v-marker-cluster": Vue2LeafletMarkerCluster,
    LPolygon,
  },
  props: [
    "sensor_1",
    "sensor_2",
    "sensor_3",
    "sensor_4",
    "sensor_5",
    "sensor_6",
    "locations",
  ],
  data() {
    return {
      zoom: 15,
      max: 18,
      min: 15,
      center: [41.15177429844501, 24.149687290191654],
      url: "https://{s}.basemaps.cartocdn.com/rastertiles/light_all/{z}/{x}/{y}.png",
      windowWidth: null,
      staticAnchor: [16, 37],
      resNoise: null,
      resTraffic: null,
      resPollution: null,
      resPerival: null,
      resSyn: null,
      resRoiyd: null,
      resWifi: null,
      resMeteo: null,
      resPoiotyd: null,
      selectedLoc: null,
      isLocopen: false,
      hasPerival: false,
      hasSyn: false,
      hasRoiyd: false,
      hasWifi: false,
      hasMeteo: false,
      hasPoiotyd: false,
      locationId: null,
      place: null
    };
  },
  methods: {
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    customIcon(cluster) {
      return new L.icon({
        iconUrl: "https://drama.smartiscity.gr/gallery/categories/context.png",
        iconSize: [35, 35], // size of the icon
        iconAnchor: [16, 16], // point of the icon which will correspond to marker's location
      });
    },
    getPerival(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resPerival = res.data;
          });
      }, 300);
    },
    getSyn(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resSyn = res.data;
          });
      }, 300);
    },
    getRoiyd(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resRoiyd = res.data;
          });
      }, 300);
    },
    getWifi(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resWifi = res.data;
          });
      }, 300);
    },
    getMeteo(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resMeteo = res.data;
          });
      }, 300);
    },
    getPoiotyd(el) {
      this.locationId = el.location_id;
      let vm = this;
      this.remove();
      setTimeout(function () {
        axios
          .get(
            `https://drama.smartiscity.gr/api/api.php?func=measurements&sensor=${el.ref_id}`
          )
          .then((res) => {
            vm.resPoiotyd = res.data;
          });
      }, 300);
    },
    remove() {
      this.resRoiyd = null;
      this.resWifi = null;
      this.resMeteo = null;
      this.resPoiotyd = null;
      this.resPerival = null;
      this.resSyn = null;
      this.isOpen = false;
      this.isLocopen = false;
      this.selectedLoc = null;
      this.hasPerival = false;
      this.hasSyn = false;
      this.hasRoiyd = false;
      this.hasWifi = false;
      this.hasMeteo = false;
      this.hasPoiotyd = false;
    },
    coords(marker) {
      let y = parseFloat(marker.location_lat);
      let x = parseFloat(marker.location_lng);
      return [y, x];
    },
  },
  computed: {
    dynamicSize() {
      return [30, 30];
    },
    dynamicAnchor() {
      return [15, 15];
    },
  },
  watch: {
    selectedLoc(el) {
      if (!el) {
        this.remove();
      } else {
        setTimeout(() => {
          this.resRoiyd = null;
          this.resWifi = null;
          this.resMeteo = null;
          this.resPoiotyd = null;
          this.resPerival = null;
          this.resSyn = null;
          this.isLocopen = true;
        }, 300);
      }
    },
    locationId(id) {
      if (id) {
        this.locations.forEach((el) => {
          if (el.location_id == id) {
            this.place = el;
          }
        });
      }
    },
  },
  mounted() {
    let vm = this;
    this.windowWidth = window.innerWidth;
    setTimeout(function () {
      vm.$refs.clusterGroup.mapObject.on("clusterclick", function (e) {
        vm.selectedLoc = null;
        setTimeout(() => {
          let newArr = [e.layer._cLatLng.lat, e.layer._cLatLng.lng];
          vm.locations.filter((el) => {
            let y = parseFloat(el.lat);
            let x = parseFloat(el.lng);
            if (_.isEqual([y, x], newArr)) {
              vm.selectedLoc = el;
            }
          });
          vm.sensor_1.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasPerival = true;
            }
          });
          vm.sensor_2.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasSyn = true;
            }
          });
          vm.sensor_3.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasRoiyd = true;
            }
          });
          vm.sensor_4.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasWifi = true;
            }
          });
          vm.sensor_5.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasMeteo = true;
            }
          });
          vm.sensor_6.forEach((el) => {
            let y = parseFloat(el.location_lat);
            let x = parseFloat(el.location_lng);
            if (_.isEqual([y, x], newArr)) {
              vm.hasPoiotyd = true;
            }
          });
        }, 300);
      });
    }, 500);
  },
  updated() {
    let vm = this;
    this.$refs.clusterGroup.mapObject.on("clusterclick", function (e) {
      vm.selectedLoc = null;
      setTimeout(() => {
        let newArr = [e.layer._cLatLng.lat, e.layer._cLatLng.lng];
        vm.locations.filter((el) => {
          let y = parseFloat(el.lat);
          let x = parseFloat(el.lng);
          if (_.isEqual([y, x], newArr)) {
            vm.selectedLoc = el;
          }
        });
        vm.sensor_1.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasPerival = true;
          }
        });
        vm.sensor_2.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasSyn = true;
          }
        });
        vm.sensor_3.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasRoiyd = true;
          }
        });
        vm.sensor_4.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasWifi = true;
          }
        });
        vm.sensor_5.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasMeteo = true;
          }
        });
        vm.sensor_6.forEach((el) => {
          let y = parseFloat(el.location_lat);
          let x = parseFloat(el.location_lng);
          if (_.isEqual([y, x], newArr)) {
            vm.hasPoiotyd = true;
          }
        });
      }, 300);
    });
  },
};
</script>

